export default {
  login(loginDetails) {
    return new Promise(function (resolve, reject) {
 /*     var user={};
      user.data = {
        role_id: "user",
        name: "yara",
        nickname: "yarik"
      };
      user.status = "success";
   user.token = {};
   resolve(user);*/
      console.log("loginDetails.password " +loginDetails.password);
      axios.post(APIENDPOINT + '/users/login',{nickName: loginDetails.nickName,password: loginDetails.password })
        .then(function (res) {
          console.log("In resolve: " + res);
          resolve(res.data);
        })
        .catch(function (err) {

          reject("login error")
        })
    });

  }
}



